import { GET_API } from "../type";

const initialState = {
  dataApi: [],
  loading: true,
};

export default function (state = initialState, action) {
  switch (action.type) {
    case GET_API:
      return {
        ...state,
        dataApi: action.payload,
        loading: false,
      };
    default:
      return state;
  }
}
